const desawisata = {
  // Fungsi render untuk menampilkan konten HTML
  async render() {
    const data = await this.fetchData('desa-wisata.json'); // Update URL
    const desaContainer = document.getElementById('desa-container');

    if (!desaContainer) {
      console.error('Desa container not found');
      return;
    }

    // Menyiapkan elemen untuk menampilkan setiap desa wisata
    let desaHTML = '';
    data.forEach(desa => {
      desaHTML += `
        <div class="card">
          <img src="${desa.photo}" alt="${desa.name}">
          <h3>${desa.name}</h3>
          <p>${desa.location}</p>
          <p>${desa.description}</p>
        </div>
      `;
    });

    // Menyisipkan HTML yang sudah disiapkan ke dalam container
    desaContainer.innerHTML = desaHTML;
  },

  // Fungsi untuk mengambil data JSON dari file atau API
  async fetchData(url) {
    try {
      const response = await fetch(url); // Mengambil data dari URL
      if (!response.ok) {
        throw new Error('Data tidak ditemukan');
      }
      const data = await response.json(); // Mengonversi respons menjadi JSON
      return Object.values(data); // Mengambil nilai-nilai objek desa dari data JSON
    } catch (error) {
      console.error('Error fetching data:', error);
      return []; // Jika gagal mengambil data, kembalikan array kosong
    }
  },

  // Fungsi afterRender jika perlu melakukan proses setelah render
  async afterRender() {
    // Implementasikan tindakan setelah render jika diperlukan
  }
};

document.addEventListener('DOMContentLoaded', () => {
  desawisata.render();
});

export default desawisata;
